import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {

		};
	},
	methods: {
		...mapMutations(['logout', 'set_role', 'set_token']),
		navTo(url) {
			switch (url) {
				case '个人资料':
					uni.navigateTo({
						url: '/pages/userinfo/userinfo'
					})
					break;
				case '关于我们':
					uni.navigateTo({
						url: '/pages/set/aboutUs/aboutUs'
					})
					break;
				case '收货地址':
					uni.navigateTo({
						url: '/pages/address/address'
					})
					break;
				case '密码修改':
					uni.navigateTo({
						url: '/pages/set/changePwd/changePwd'
					})
					break;
			}
		},
		//退出登录
		toLogout() {
			uni.showModal({
				content: '确定要退出登录么',
				success: (e) => {
					if (e.confirm) {
						uni.clearStorageSync('local_token');
						this.set_token('')
						this.logout();
						this.set_role(2);
						setTimeout(() => {
							uni.reLaunch({
								url: '../public/login'
							})
						}, 200)
					}
				}
			});
		},
	}
}
